import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';

import AnchorLink from 'react-anchor-link-smooth-scroll';

import { Container } from '@components/global';
//import Call2Act from '@ui/Call2Act';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        masthead: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "masthead_product" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1400) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Container>
          <Grid>
            <Fade>
            <Text>
              <h1>
              Say hey to Hai !
              </h1>
              <br/>
              <p>
              We're happy to introduce Rearia Hai — the first modular earphone that's universal for hearing aid users, cochlear implant users, and hearing people alike.<br/> 
              </p>
              <Call2Button><AnchorLink href="#intro">Explore more!</AnchorLink></Call2Button>
              {/*<Call2Act text="Explore our campaign" href="#intro"/>*/}
            </Text>
            </Fade>
          </Grid>
          <Art>
            
            <Img fluid={data.masthead.childImageSharp.fluid} />
          </Art>
        </Container>
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.primary};
  padding-top: 86px;
  height: 100vh;
  overflow: hidden;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding-top: 128px;
  }
`;

const Art = styled.div`
  width: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  @media (max-width: ${props => props.theme.screen.md}) {
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(to bottom, rgba(255,245,238, 1) 50%, rgba(255,245,238,0.3)); 
      transition: all .3s linear;
    }
  }

  > div {
    width: 100%;
    margin-bottom: -14.5%;

    @media (max-width: ${props => props.theme.screen.md}) {
      height: 100%;
      width: auto;
      margin-top: 20%;
    }
    @media (max-width: ${props => props.theme.screen.sm}) {
      margin-top: 55%;
    } 
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 5fr;
  align-items: center;
  grid-gap: 75px;
  z-index: 2;
  top: 17vh;
  left: 0;
  position: relative;
  @media (max-width: ${props => props.theme.screen.lg}) {
    top: 15vh;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;
    top: 10vh;

    > ${Art} {
      order: 1;
    }
  }

  @media (max-width: ${props => props.theme.screen.sm}) {
    top: 0vh;
  }
`;

const Text = styled.div`
  justify-self: start;
  @media (max-width: ${props => props.theme.screen.md}) {
    justify-self: start;
  }
`;


const Call2Button = styled.div`
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  border-radius: 100px;
  border: 2px solid;
  border-color: ${props => props.theme.color.grayscale.S1000};
  line-height: 24px;
  padding: 11px 25px;
  margin: 35px 0;
  -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  ${props =>
    props.accent &&
    `border-color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
  }}`};

  a {
    text-decoration: none;
    color: ${props => props.theme.color.grayscale.S1000};
    -webkit-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.white.regular
          : props.theme.color.grayscale.S1000
    }}`};
  } 
  a:hover{
    text-decoration: none !important;
  }
  div {
    display:inline-block;
    margin-right: 5px;
    -webkit-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
  }
  &:hover {
    a{
      color: ${props => props.theme.color.secondary.S400};
    }
    div {
      margin-right: 15px;
    }
    border-color: ${props => props.theme.color.secondary.S400};
  }

  
`

export default Header;
