import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import ImgSingle from '@ui/img_single';
//import Video from '@ui/video';


const Intro = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Music: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "music" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "anywhere" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro">
        <SectionHeader 
          header="A common problem for many hearing aid and cochlear implant users" 
          subheader="It’s not likely that any style of earbuds or earphones are going to work with hearing aids. The reason is quite straightforward — hearing aids already sit within ears, so fitting another device into the ears isn't possible."
        />
        {/*<Video
          header="The first earphone for all hearing conditions" 
          text="We are launching a campaign for producing a brand new earphone design for you — no matter you are normal hearing, a hearing aid user, or a cochlear implant user."
          video="398411002"
          togglecover="false"  
          toggletop="true"       
        />*/}
        <ImgSingle
          header="The first earphone for all hearing conditions" 
          text="We're launching a campaign to create a new, innovative earphone designed for you — whether you are normal hearing, a hearing aid user, or a cochlear implant user."
          video="398411002"
          togglecover="false"  
          toggletop="true"      
        >
          <Img fluid={data.Earphone4all.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxtInverse 
          header="Universal and modular design" 
          text="Rearia Hai is the first earphone that is universal for all hearing aid users, cochlear implant users, and hearing people. Thanks to its modular design with telecoil technology, you can wear it no matter whether you wear bilateral aids, single-sided, or just no aids at all. You can quickly and easily switch between the earbuds and coils and mix them as you want." 
        >
          <Img fluid={data.Unidesign.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgTxt 
          header="Experience superior sound quality" 
          text="Telecoil technology is an established technology which is compatible with most hearing aids and cochlear implants. We developed this versatile technology further to provide outstanding sound quality for enjoying music."
        >
          <Img fluid={data.Music.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Stop the buzz" 
          text="For even further improved listening experience, we developed the first technology to cancel any bothering interference from nearby electrical infrastructure. We call it DBC (Dynamic Buzz Cancellation). It lets you enjoy music free from buzzing in difficult environments — such as in a plane or near power systems." 
        >
          <Img fluid={data.Noise.childImageSharp.fluid} />
        </ImgTxtInverse> 
        <ImgSingle
          header="Adjust with the App" 
          text="With Hai app, we provide even more! Change your preferred equalizer settings and save them to suit all situations. It also gives guidance to change the stereo balance of your phone to fit your hearing condition." 
          togglecover="true"  
          toggletop="false"       
        >
           <Img fluid={data.Mobileeq.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="Intuitive and seamless usage" 
          text="Convenient design for easy usage with three buttons operation — answer calls, change tracks and adjust volume easily. You can also activate your smartphone assistant for seamless integration with your smartphone."    
        >
          <Img fluid={data.Ui.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Clear conversations" 
          text="Call your loved ones, friends or business clients with the built-in microphone — integrated with technology which provides clear voice and low latency." 
        >
          <Img fluid={data.Conversation.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgSingle
          header="Express your style" 
          text="Customize your Hai with different coloured surfaces and stickers! Let your creativity run free and let your device show your style."
          togglecover="true"  
          toggletop="false"       
        >
          <Img fluid={data.Stickers.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="With you anywhere, anytime" 
          text="Whatever the weather might be, you can bring Hai anywhere with you. Lightweight design allows it to easily clip to your clothes — or wear it like a necklace! Water resistant and modern design lets you wear it while exercising, working or just relaxing and enjoying music."  
        >
          <Img fluid={data.Anywhere.childImageSharp.fluid} />
        </ImgTxt>  
      </Section>
    )}
  />
);

export default Intro;
