import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Container } from '@components/global';
import Fade from 'react-reveal/Fade';

const ImgTxtInverse = ({ header, text, children, caption}) => (
  
  <Container>
    <HeaderWrapper>
      <Grid>
        <Art>
          {children}
          <Caption>{caption}</Caption>
        </Art>
        <div>
          <Fade>
            <h3>{header}</h3>
            <p>{text}</p>
          </Fade>
        </div>
      </Grid>
    </HeaderWrapper>
  </Container>
);

ImgTxtInverse.propTypes = {
  children: PropTypes.element.isRequired,
};

const HeaderWrapper = styled.div`
  padding: 40px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 0;
    margin-left: 0%;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 50px;
  text-align: left;
  align-items: center;
  justify-items: center;

  ${props =>
    props.inverse &&
    `
    text-align: left;
    grid-template-columns: 2fr 1fr;
  `}

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    grid-gap: 15px;

    &:last-child {
      margin-bottom: 0px;
    }

    ${props =>
      props.inverse &&
      `
        ${Art} {
          order: 2;
        }
    `}
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    order: 1;
  }
`;


const Caption = styled.div`
  padding-top:5px;
  padding-right: 30%;
  ${props => props.theme.font_size.xsmall};
`;

export default ImgTxtInverse;
