import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';

import { Section, Container } from '@components/global';
import SectionHeader from '@ui/sectionheader';

import gifFeatures from '@images/spec/gif_features.gif';

const SPECS = [
  {
    name: 'Superb sound quality',
    image: 'icon-04.png',
    role: 'Designed by Hi-Fi experts for maximum sound quality. Works with both hearing aids and cochlear implants.',
  },
  {
    name: 'Dynamic Buzz Cancellation',
    image: 'icon-02.png',
    role: 'Innovative technology reduces noise from electrical infrastructure nearby, a common problem for hearing aid users.',
  },
  {
    name: 'Intuitive interface',
    image: 'icon-05.png',
    role: 'Convenient usage with three buttons. Change volume and tracks, answer calls or activate your smartphone assistant.',
  },
  {
    name: 'Universal design',
    image: 'icon-07.png',
    role: 'Modular and interchangeable coils and earbuds work for both hearing aids, cochlear implants and hearing people. Can also be used with single sided hearing aids.',
  },
  {
    name: 'Adjust EQ with Hai app',
    image: 'icon-03.png',
    role: 'With Hai app you can easily adjust the equalizer settings and save them as favourites for different listening situations.',
  },
  {
    name: 'Wear it anywhere',
    image: 'icon-06.png',
    role: 'Lightweight and water resistant, you can wear Hai around your neck or clip to your clothes.',
  },
  {
    name: '6 hours wireless playtime',
    image: 'icon-01.png',
    role: 'Cordless listening for whole workday with 1300mAh battery. Up to 30 hours when using earphones.',
  },
];



const TechSpec = () => (
  <StaticQuery
    query={graphql`
      query {
        allFile(filter: { sourceInstanceName: { eq: "spec" } }) {
          edges {
            node {
              relativePath
              childImageSharp {
                fluid(maxWidth: 400, maxHeight: 400) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        },
        Features: file(
          sourceInstanceName: { eq: "spec" }
          name: { eq: "features" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="features" accent="secondary">
        <SectionHeader header="Key Features" subheader="" />
        <Container style={{ position: 'relative' }}>
          <Grid>
            <SpecMainImg>
              <Fade>
                <Art>
                <img src={gifFeatures} alt="Product Hai"/>
                </Art>
              </Fade>
            </SpecMainImg>
            <SpecGrid>
              {SPECS.map(({ name, image, role }) => {
                const img = data.allFile.edges.find(
                  ({ node }) => node.relativePath === image
                ).node;

                return (
                  <SpecItem>
                    <Art>
                      <Img fluid={img.childImageSharp.fluid} alt={name} />
                    </Art>
                    <div>
                      <Fade>
                      <Title>{name}</Title>
                      <p>{role}</p>
                      </Fade>
                    </div>
                  </SpecItem>
                );
              })}
            </SpecGrid>
          </Grid>
        </Container>
      </Section>
    )}
  />
);


const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 5fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const SpecMainImg = styled.div`
  height: 100%;
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    width: 50%;
    margin: 0 auto;
  }
`;

const SpecGrid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content;
  grid-gap: 30px;
  justify-content: space-between;
  width: 100%;
  margin-top: 32px;

  @media (max-width: ${props => props.theme.screen.lg}) {
    justify-content: start;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100%;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  }

  @media (max-width: ${props => props.theme.screen.xs}) {
    grid-gap: 24px;
  }
`;

const SpecItem = styled.div`
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 30px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr 2fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
`;

const Title = styled.p`
  margin-bottom: 10px;
  color: ${props => props.theme.color.black.regular};
  font-weight: bold;
`;

export default TechSpec;
